import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import * as serviceWorker from "./serviceWorker";
import MainPage from "./MainPage";
import NavicationBar from "./NavicationBar";
import CategoriesList from "./CategoriesList";
import SearchPage from "./SearchPage";
import FavoritePage from "./FavoritePage";
import RecentlyAdded from "./RecentlyAdded";
import CreateMeal from "./CreateMeal";
import MealViewer from "./MealViewer";
import { userData } from "./Data";
import onload from "./onLoad";
import Welcome from "./welcome";

onload();

export let toEnglishNumber = function (strNum) {
  var ar = "٠١٢٣٤٥٦٧٨٩".split("");
  var en = "0123456789".split("");
  strNum = strNum.replace(/[٠١٢٣٤٥٦٧٨٩]/g, (x) => en[ar.indexOf(x)]);
  strNum = strNum.replace(/(?![.])[\D]/g, "");
  return strNum;
};

export let updateRenderState = function (component) {
  this.setState({ renderPage: component });
};

export default class Index extends Component {
  constructor() {
    super();
    this.state = {
      renderPage: window.localStorage.getItem("firstTime") ? (
        [<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />]
      ) : (
        <Welcome key={Math.random() * 100} />
      ),
    };
    updateRenderState = updateRenderState.bind(this);
  }

  componentDidMount() {
    window.history.replaceState("", "", "/");
    window.addEventListener("popstate", this.checkURL);
    window.resizeBy(350, 800);
  }

  checkURL = () => {
    const [path, hash] = [window.location.pathname, window.location.hash];
    window.localStorage.setItem("userData", JSON.stringify(userData));

    if (path.includes("search")) {
      this.setState({ renderPage: <SearchPage key={Math.random() * 100} /> });
    } else if (path.includes("categoriesList")) {
      this.setState({ renderPage: <CategoriesList key={Math.random() * 100} /> });
    } else if (
      path === "/favorites" &&
      !hash.includes("#MealViewer") &&
      (hash.includes("#breakfast") || hash.includes("#lunch") || hash.includes("#dinner") || hash.includes("#snacks"))
    ) {
      this.setState({ renderPage: <FavoritePage key={Math.random() * 100} /> });
    } else if (
      (path === "/favorites" && hash.length === 0) ||
      (path === "/favorites" && hash.includes("#Meals") && !hash.includes("#MealViewer"))
    ) {
      this.setState({ renderPage: [<FavoritePage key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />] });
    } else if (path === "/CreateNewMeal/favorites") {
      this.setState({ renderPage: <FavoritePage key={Math.random() * 100} /> });
    } else if (path === "/CreateNewMeal") {
      this.setState({ renderPage: <CreateMeal key={Math.random() * 100} /> });
    } else if (path === "/CreateNewMeal/recentlyAdded") {
      this.setState({ renderPage: <RecentlyAdded key={Math.random() * 100} /> });
    } else if (path === "/EditMeal/favorites") {
      this.setState({ renderPage: <FavoritePage key={Math.random() * 100} /> });
    } else if (path === "/EditMeal") {
      this.setState({ renderPage: <CreateMeal key={Math.random() * 100} editMode={true} /> });
    } else if (path === "/EditMeal/recentlyAdded") {
      this.setState({ renderPage: <RecentlyAdded key={Math.random() * 100} /> });
    } else if (hash.includes("MealViewer")) {
      this.setState({ renderPage: <MealViewer key={Math.random() * 1000} /> });
    } else if (path === "/recentlyAdded") {
      this.setState({ renderPage: <RecentlyAdded key={Math.random() * 100} /> });
    } else if (path === "/") {
      this.setState({
        renderPage: [<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />],
      });
    }
  };

  render() {
    return <Fragment>{this.state.renderPage}</Fragment>;
  }
}
ReactDOM.render(<Index />, document.getElementById("root"));
serviceWorker.register();
